.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* GLOBAL CSS */


.row-flex {
  display: flex;
  flex: 1;
  flex-direction: row;
}

.col-flex {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.max-col-flex {
  display: flex;
  flex: 1;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.viewport {
  height: 100vh;
  /* width: 100vw; */
  padding: 0px !important;
  display:flex;
  flex-direction: column;
  /* font-family: Roboto; */
}
